@use "../base/variables" as v;
@mixin cta-arrow {
&:after {
            content: "";
            background: url(../img/Arrow-text-link.svg) no-repeat 0px 6px;
            background-size: contain;
            width: 15px;
            height: 16px;
            display: inline-block;
            transform: translateX(0);
            transition: transform .3s ease;
            margin-left: 6px;
            animation: arrowfx 0.6s ease-in-out forwards;
        }
}

//login-page and popup
.login-reg-block{
.signup-box{text-align: center; margin-bottom: 2rem;
img{margin: 0 auto;}
p{color: v.$white; line-height: 18px; margin: 1.5rem 0;}
}
.splide__pagination__page.is-active{
  background: v.$white !important;
  transform: scale(1);
  &::before{
     display: none;
  }
}
.subhead {
    color: #0c2740;
    font-size: .75rem;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 1rem;
    letter-spacing: .5px;
}
}
.asia-logomark{
  img{width: 200px; margin: 0 auto 1.5rem auto;}
}
.navbar__signupbox {
.splide__pagination__page {
    background: #ccc !important;
    border: 0;
    border-radius: 50%;
    display: inline-block;
    height: 8px;
    margin: 3px;
    opacity: .7;
    padding: 0;
    position: relative;
    transition: transform .2s linear;
    width: 8px;
     h3.subhead{color: v.$white;}
}
}
//end
//Current Job Openings career page
.current-openings{ margin: 0; padding: 0; list-style: none; 
li{ margin-bottom: 0; border-bottom: solid 1px #ddd;
  justify-content: space-between; 
  align-items: center; 
  flex-direction: row; 
  display: grid;
  padding-top: 2rem; padding-bottom: 2rem; 
  grid-template-columns: 540px 210px auto;
  gap: 2rem;
}
.time-blk{display: flex; gap: 1.5rem;}
.title-blk{font-family: v.$font-family-Poppins; font-weight: v.$font-weight-SemiBold; font-size: 24px; line-height: 34px;
  a{color: #0C2740;}
}
}
//end
.scroll-menubar{display:flex; justify-content: space-between; gap: 2rem; align-items: center; flex-wrap: wrap;
a{color: #0C2740; font-weight: v.$font-weight-Medium; font-family: v.$font-family-Poppins;}
.btn {color: v.$white;}
}
.left-align-menu{display: flex; flex-wrap: wrap; gap: 2.5rem;}
.experts-card-location {display: flex; flex-wrap: wrap; gap: 47px; justify-content: flex-start;
.location-card-view{display: none;  animation: fadeIn 0.3s ease;
&.visible {
      flex-basis: 100%;
      display: block;
    }
}

&.asiapedia-card{
.image-card{
  width: 100%;
  height: 198px;
  &::before{ display: none;}
  img{border-radius: 4px;}
}
.image-card-text{color: #222831; position: inherit; font-size: 1rem; font-weight: v.$font-weight-Medium; padding-left: 0; padding-right: 0;}
.image-card-wrapper{max-width: 352px;}
}
}
.find-expert-list{
  &.find-asiapedia-list {
    .expert-country .radio-group-wrapper{
  //max-width: 356px; width: 356px; border: none;
  max-width: 250px; width: 250px; border: none;
  
}
.pagination{ margin-left: 0px; justify-content: flex-start; margin-bottom: 0;}
.type-label{font-size: 1rem; font-weight: v.$font-weight-SemiBold; margin-bottom: 1.2rem;}
  }
.experts-detail{border: none;}
}

//need to finalize after approval
.card-horizontal{
  &.home-top-service-block{ align-items: start;gap: 3rem;
  .card-text{font-weight: 300;}
  .btn{margin-top: 0.5rem;}
  .card-body {
    .service-left {display: grid;margin-top: 0.6rem; gap: 1.2rem;
    &.service-left-2column{column-count: 2;column-gap: 3.3rem; list-style: none; display: inherit;
    li{margin-bottom: 1.3rem;}
    }
    .text-cta-link { color: #217FD4;}
    li{
      &:hover {
        a {transition: transform .4s ease;
        @include cta-arrow;
        }
      }
      a {font-weight: 500;}
    }
    }
  }
  }
}
//end
//speekar list
.our-team-experts {
  .triangle {
  width: 0;
  height: 0;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-top: 25px solid #dddddd;
  position: absolute;
  bottom: -3px;
  right: -16px;
  transform: rotate(314deg);
}
.splide__slide{
  &:nth-child(4n + 1) .triangle {
  border-color: #FFE8AF;
}
&:nth-child(4n + 2) .triangle{
  border-color: #EAA68F;
}
&:nth-child(4n + 3) .triangle{
  border-color: #ADCAA7;
}
&:nth-child(4n + 4) .triangle{
  border-color: #C1C5DC;
}
}
}
//end
/* Register for Our Expert-Led Sessions home page */
.card-resrcs {
    display: flex;
    flex-direction: row;
    background: v.$white;
    border-radius: 10px;
    align-items: flex-start;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  }
  .resrcs-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  .resrcs-body {
    padding: 1rem;
  }
  .resrcs-date {
    font-size: 0.9rem;
    color: #666;
    margin-bottom: 0.5rem;
  }
  .resrcs-date.overlay {
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(12, 39, 36, 0.8);
    color: white;
    padding: 2rem;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    text-align: center;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .resrcs-date.overlay span {
    font-size: 1.3rem;
    font-weight: bold;
  }
  .resrcs-title {
    font-size: 17px;
    font-weight:v.$font-weight-Medium;
    margin: 0.25rem 0 0.5rem;
    line-height: 26px;
    font-family: v.$font-family-inter;
    a{color: #222831;}
  }
.resrcs-meta {
display: flex;
flex-direction: row;
justify-content: flex-start;
gap: 1rem;
margin-top: 1rem;
margin-left: 1.5rem;
font-size: 14px;
color: #9A9A9A;
font-weight: 500;
line-height: 24px;
}
  /* news */
.card-resrcs{
  //news overwrite
  &.news-card{  margin-bottom: 2rem; align-items: center;
    .resrcs-image{width: 300px; height: 180px;}
      .resrcs-body{ padding-left: 3rem;}
      .resrcs-meta{padding: 0; margin: 0;}
    }
  //related event overwrite  
  &.related-card{flex-direction: column; max-width: 516px; border-radius: 4px;margin: 0 auto 2rem auto;
    .resrcs-meta{ margin: 0; margin-bottom: 1rem;font-weight: 400;
    a{color: #344C71; font-weight: 400;}
  }
.resrcs-title{font-size: 1rem;}
.resrcs-body{padding-left: 1.5rem; padding-right: 1.5rem;}
.resrcs-date.overlay{min-width: 128px; background: rgba(12, 39, 64, .56); font-size: 1rem; line-height: normal;
span{font-size: 2rem;}
}
.resrcs-image{height: 170px;}
&.related-card-small{
  max-width: 360px;
.resrcs-body{
  min-height: 182px;
}
}
} 
//Speaking Engagements overwrite
  &.speaking-card{
        flex-direction: row;
        .resrcs-image{
            width: 180px; height: 130px;
          }
      }
//webinar overwrite
    &.webinar {
        margin-bottom: 2rem; align-items: center;
        .resrcs-image{
            width: 35.8%; height: 180px;
          }
          .resrcs-body{padding: 1rem 3.5rem; width: 100%; 
            .resrcs-meta{padding: 0; margin: 0 0 0.5rem 0;
            }
            .expt-location{
            a{color: #217FD4;}
            }
            .resrcs-title{margin-bottom: 1.5rem;}
        }

      }
    &.small-webinar{margin-bottom: 1rem;
          .resrcs-image{
            width: 176px;
            height: 128px;
          }
          .resrcs-body{
            padding: 1rem 1rem 0rem 2rem; width: 72%; min-height: 128px;
            .resrcs-title{font-size: 14px; font-weight: v.$font-weight-Medium; line-height: 24px;}
          }
          .resrcs-meta{margin-left: 0;}
        }
  }
  .expt-location{display: flex; gap: 5px;}
  
// pagination
.pagination {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 4rem 0 3rem 0;
    width: 93%;
    a {
        color: #222831;
        float: left;
        padding: 8px 16px;
        text-decoration: none;
        transition: background-color .3s;
        margin: 0 4px;
        border-radius: 5px;
        &.active {
            background-color: #ebeef4;
            color: #222831;
            
          }
          &:hover:not(.active) {background-color: #ebeef4;}
      }
  }
  //end

@media (max-width: 1220px) {
.card-horizontal.home-top-service-block .card-body .service-left.service-left-2column{
  column-gap: 1.5rem;
}
}

@media (max-width: 1024px) {
    .current-openings {
    li{flex-direction: column;   grid-template-columns: auto 210px auto;
        align-items: flex-start;
      }
     
  }
     .left-align-menu{gap: 1.5rem;} 
  .card-resrcs{
    &.webinar{
     .resrcs-body{padding-left: 2rem; padding-right: 2rem}
  }
  }
}
@media (max-width: 906px) {
 .experts-card-location {
    justify-content: start;
  }
}
@media (max-width: 767px) {
  .find-expert-list.find-asiapedia-list {
    .expert-country .radio-group-wrapper{max-width: 100%; width: auto;}
    .pagination{ margin-left: 0;}
  }

  .card-resrcs.related-card{width: auto;}
  .pagination {
    margin: 4rem 0 3rem 0;
    width: 100%;
    a {
        padding: 4px 8px;
        margin: 0 2px;
        font-size: 14px;
      }
  }
  .current-openings {flex-direction: column;  
    li{flex-direction: column;   grid-template-columns:repeat(1,1fr);
        align-items: flex-start;
      }
  }
    .card-resrcs{
    &.webinar{border-radius: 0; box-shadow: none;
  .resrcs-image{ border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;}
  }
  }
        
}
@media (max-width: 667px) {
     .experts-card-location {
    justify-content: center;
  }
}
@media (max-width: 557px) {
  .card-resrcs{
    &.webinar{flex-direction: column;
  .resrcs-image{ max-width: 100%; width: 100%;}
  .resrcs-body{padding-left: 0px; padding-right: 0;}

  }
    &.small-webinar{flex-direction: column;
    .resrcs-image{ max-width: 100%; width: 100%;}
    .resrcs-body{width: 100%;}
    
  }
  }
  .card-horizontal{
    &.home-top-service-block {
      .service-left li:hover {
        a{color: #217FD4;
          &:after {display: none; }
        }
      }
    }
  }
}
