@use "../base/variables" as v;
/**Black overley  and blue overlay*/
@mixin gradient-overlay {
  &::after {
    display: block;
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(
      to left,
      rgba(0, 0, 0, 0.5) 20%,
      rgba(0, 0, 0, 0.9) 70%,
      rgba(0, 0, 0, 0.9) 100%
    );
    opacity: 0.8;
    z-index: 0;
  }
}
.banner-image-overley {
  @include gradient-overlay;
}
/**end*/
/**footer cta banner*/
.hero-section {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  min-height: 400px;
  overflow: hidden;
  .hero-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
  }
  .hero-content {
    position: relative;
    z-index: 1;
    max-width: 616px;
    padding: 2rem 0rem 2rem 0rem;
    color: v.$white;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    //align-items: center;
  }
  .hero-text {
    font-size: 18px;
    line-height: 1.6;
    font-family: "Inter";
    font-weight: 400;
    color: v.$white;
  }
  .text-btn {
    width: 100%;
    .toggleBtn-dropdown-button::after{border-color: v.$white;}
    .toggleBtn-dropdown-button{color: v.$white;}
    .toggleBtn-dropdown-menu{ margin-top: 0;
    a{padding-top: 0.25rem; padding-bottom: 0.25rem;}
    }
  }
}
/**end*/
.masthead {
  position: relative;
  height: 420px;
  background: linear-gradient(
    90deg,
    v.$dsa-dark-blue 0%,
    v.$dsa-medium-blue 100%
  );
  background-size: cover;

  h1 {
    color: v.$white;
    max-width: 600px;
    line-height: 1.2;
    font-size: 2rem;
    font-family: v.$font-family-Poppins;
    font-weight: v.$font-weight-SemiBold;
    margin-bottom: 0;
    a {
      color: v.$white;
    }
  }
  &__text {
    //position: relative;
    position: absolute;
    height: inherit;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 1rem;

    &.justify-content-center {
      justify-content: center;
    }

    p{color: v.$white; margin-bottom: 0;}
    h3{font-size: 14px; font-family: v.$font-family-inter; font-weight: v.$font-weight-light; color: v.$white; text-transform: uppercase;}
    .feature-events{
    ul{margin:0; padding: 0; list-style: none;      display: grid;
          grid-template-columns: repeat(3, 1fr);
          gap: 2rem;
    li{font-size: 15px; font-weight: v.$font-weight-Medium;border-right: solid 1px v.$white;
      padding-right: 1rem; display: flex; gap: 1rem; align-items: start;

      a{color: v.$white; font-weight: bold;}
      &:last-child{border: none;}
      .news-date{font-size: 12px; color: v.$white; font-weight: normal;}
    }
    }
&.feat-news{
  li{flex-direction: column; gap: 0.8rem;}
}
  }
  .label-text{color: v.$white;}
  }
  &__bg {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: inherit;
    overflow: hidden;
    img {
      //position: absolute;
      top: 50%;
      left: 50%;
      //transform: translate(-50%, -50%);
      //transform: translate(-50%, -50%) scale(1.3);
      width: auto;
      height: auto;
      min-width: 100%;
      min-height: 100%;
    }
  }

  &.event-hero{
    height: 700px;
    p{font-weight: v.$font-weight-normal;}
    .masthead__text{ justify-content: space-evenly;  padding: 0; padding-right: 1.5rem;
      h1{font-size: 38px; line-height: 50px; 
      //font-family: v.$font-family-Poppins; 
      //font-weight: v.$font-weight-SemiBold; 
      margin-bottom: 0.8rem;}
      .text-head{margin-top: 3rem; max-width: 700px;
         .btn-outline-white{ margin-top: 2rem;}
      }
      h3{margin-bottom: 1rem;}
    }
    &.event-hero-l2{ height: 640px;
      .text-head{max-width: 800px; margin-top: 0;}
      h1{max-width: 100%;}
      p{margin-top: 2rem; margin-bottom: 2.5rem; font-size: 16px; line-height: 30px; 
        font-weight: v.$font-weight-SemiBold;
      }
    }
    &.news-hero-inner{height: 425px;
    h1{max-width: 100%; 
      //font-weight: v.$font-weight-SemiBold; 
      font-size: 2rem; line-height: 45px;}
    }
  &.asiapedia-hero{
    .text-head{max-width: 660px; margin-top: 6rem;
    h1{max-width: inherit;}
    
    }
    .feature-events{padding-top: 5rem;}
  }
  
  }
  &.personnel-hero-inner{height: 419px;
    h1{max-width: 100%;}
    }
    &.asiapedia-inner{
    .text-head{max-width: 460px; 
    }

  }

&.small-banner-height{height: 210px;}

&.cnt-min-h{
  .text-head{
    max-width: 620px;
  }
}
}
/* end */
.masthead-logo{position: absolute; z-index: 1; top: 45%; right: 16px;}
.breadcrumb {
  padding-top: 1.5rem;
  ul {
    display: flex;
    padding: 1rem 0;
    margin: 0px;

    li {
      padding: 0 1.5rem 0 0.5rem;
      font-size: 0.9rem;
      &:first-child {
        padding-left: 0px;
      }
    }
    a {
      color: #C0C0C0;
      font-size: 0.9rem;
    }
  }
      &.justify-content-end{
    justify-content: flex-end;
    display: flex;
    position: absolute;
    z-index: 3;
    flex-direction: inherit;
    align-items: end;
    height: inherit;
    padding: 1rem;
    }
}

@media (max-width: 1366px) {
  .hero-section{
    min-height: 240px;
    .hero-content{gap: 0;}
  }
}
//Tablets
@media (max-width: 1100px) {
  .masthead { height: 222px;
    &__bg img {
      height: auto;
      max-width: fit-content;
    }
    &__text{
  .feature-events{
    ul{
      grid-template-columns: repeat(1, 1fr);
      gap: 2rem;
    li{border-right: none;
    }
    }
  }
    }
&.cnt-min-h{ height: 320px;}
&.asiapedia-inner {height: 320px;}
&.personnel-hero-inner{height: 220px;}
  }
  
  .hero-section {
     .section-wrapper-min{margin: 0;}
    .hero-content {
     max-width: 556px;
    }
  }
  .masthead-logo{display: none;}
}

@media (max-width: 642px) {
.masthead{
  &.event-hero{height: 700px;
.masthead__text{justify-content:space-evenly;
.text-head{margin-top: 0;}
h3{margin-top: 3rem}
}
&.news-hero{height: 850px;}
}
&.asiapedia-hero{height: 1000px;
.masthead__text{justify-content: center;
.text-head{margin-top: 0;}
}
}
}
.masthead.cnt-min-h{ height: 100%;
.text-head{
  .btn{margin-bottom: 1rem;}
}
.masthead__text{position: relative;}
}
}

@media (max-width: 556px) {
  .breadcrumb ul{flex-wrap: wrap;}
    .hero-section {
     @include gradient-overlay; 
    .hero-content {
     max-width: 100%;
    }
  }
  .masthead.event-hero.event-hero-l2{height: 720px;}
}

@media (min-width: 768px) {
  .hero-section {
    .hero-content {
      padding: 2rem 1rem 2rem 0rem;
    }
  }
}